import React, { useState, useEffect, useCallback } from "react";
import "./App.css";
import "bootstrap/dist/css/bootstrap.css";
import {
  Pagination,
  getLastPage,
  getVisibleResultsMax,
  getVisibleResultsMin,
} from '@workday/canvas-kit-react/pagination';

function App() {
  const [searchType, setSearchType] = useState(() => {
    return localStorage.getItem("searchType") || "date_time";
  });
  const [searchValue, setSearchValue] = useState("");
  const [filteredResults, setFilteredResults] = useState([]);
  const [currentPage, setCurrentPage] = useState(1);
  const [totalPages, setTotalPages] = useState(1);
  const [totalRecord, setTotalRecord] = useState(1);
  const [isSearched, setIsSearched] = useState(false); // Trng thi kim tra  tm kim hay cha
  const [loading, setLoading] = useState(false); // Trng thi kim tra d liu ang c ti v
  const [noResults, setNoResults] = useState(false); // Trng thi khng c kt qu

  // Hm fetch d liu
  const fetchData = useCallback(
    async (page = currentPage) => {
      setLoading(true);
      setNoResults(false);
      try {
        const response = await fetch(
          `/search?attribute=${searchType}&data=${searchValue}&page=${page - 1}` // Chuyn `page` thnh s trang bt u t 0
        );
        if (!response.ok) {
          throw new Error("Network response was not ok");
        }
        const result = await response.json();
        const processedData = result.data.map((item) => {
          const fields = item.split(",");
          return {
            date_time: fields[0].replace(/"/g, ""),
            trans_no: fields[1],
            credit: fields[2],
            debit: fields[3],
            detail: fields[4].replace(/"/g, ""),
          };
        });

        setFilteredResults(processedData);
        setTotalRecord(result.totalRecords);
        setTotalPages(result.totalPages);
        setIsSearched(true);

        if (processedData.length === 0) {
          setNoResults(true);
        }
      } catch (error) {
        console.error("Failed to fetch data:", error);
      } finally {
        setLoading(false);
      }
    },
    [currentPage, searchType, searchValue]
  );


  // Hm x l s kin tm kim
  const handleSearch = () => {
    setCurrentPage(1); // t li trang v trang 1 khi tm kim
    setIsSearched(false); // t li trng thi tm kim trc khi gi API
    fetchData(); // Gi API  ly kt qu
  };

  // Hm x l s kin chuyn trang
  const handlePageChange = (pageNumber) => {
    if (pageNumber !== currentPage) {
      setCurrentPage(pageNumber); // Cp nht `currentPage`
      fetchData(pageNumber); // Gi API vi s trang
    }
  };



  // Hm highlight vn bn
  const highlightText = (text) => {
    if (!searchValue) {
      return text;
    }
    const regex = new RegExp(`(${searchValue})`, "gi");
    return text.replace(regex, (match) => `<span class="highlight">${match}</span>`);
  };

  // Hm iu kin  ch highlight ct tng ng
  const renderCell = (item, field) => {
    const value = item[field];
    return (
      <td
        dangerouslySetInnerHTML={{
          __html: searchType === field ? highlightText(value) : value,
        }}
      ></td>
    );
  };

  // Thit lp phn trang vi @workday/canvas-kit-react/pagination
  const resultCount = filteredResults.length;
  const lastPage = getLastPage(resultCount, totalPages);

  // Hm  reload li trang
  const handleReload = () => {
    window.location.reload();
  };

  useEffect(() => {
    localStorage.setItem("searchType", searchType);
  }, [searchType]);

  return (
    <div className="App">
      <h1>Tra cu d liu Sao K</h1>

      {/* Tm kim */}
      <div className="search-options">
        <button
          onClick={() => {
            setSearchType("date_time");
            setSearchValue(""); // Reset gi tr tm kim
            setIsSearched(false); // t trng thi l cha tm kim
            window.location.reload(); // Reload trang
          }}
        >
          Date_Time
        </button>
        <button
          onClick={() => {
            setSearchType("trans_no");
            setSearchValue("");
            setIsSearched(false);
            window.location.reload(); // Reload trang
          }}
        >
          Trans_no
        </button>
        <button
          onClick={() => {
            setSearchType("credit");
            setSearchValue("");
            setIsSearched(false);
            window.location.reload(); // Reload trang
          }}
        >
          Credit
        </button>
        <button
          onClick={() => {
            setSearchType("debit");
            setSearchValue("");
            setIsSearched(false);
            window.location.reload(); // Reload trang
          }}
        >
          Debit
        </button>
        <button
          onClick={() => {
            setSearchType("detail");
            setSearchValue("");
            setIsSearched(false);
            window.location.reload(); // Reload trang
          }}
        >
          Detail
        </button>
      </div>

      <div className="search-input">
        <input
          type={["credit", "debit"].includes(searchType) ? "number" : "text"}
          placeholder={`Nhp ${searchType === "date_time"
            ? "Ngy/Thng/Nm"
            : searchType === "trans_no"
              ? "Trans_no"
              : searchType === "credit"
                ? "Credit"
                : searchType === "debit"
                  ? "Debit"
                  : "Detail"
            }`}
          value={searchValue}
          onChange={(e) => setSearchValue(e.target.value)}
        />
        <button onClick={handleSearch}>Tm kim</button>
      </div>

      {/* Hin th trng thi tm kim */}
      {loading && <p>ang tm kim...</p>}

      {/* Kt qu tm kim */}
      {isSearched && !loading && ( // Ch hin th kt qu nu  tm kim v khng cn trng thi loading
        <div className="results">
          <p>Tng s kt qu: {totalRecord}. Tng s trang: {totalPages}</p> {/* Hin th tng s bn ghi */}
          {noResults && (
            <p className="error-message">Khng tm thy kt qu tm kim</p>
          )}
          <h2>Kt qu tm kim:</h2>
          {noResults && (
            <p className="error-message">Khng tm thy kt qu tm kim</p>
          )}
          {filteredResults.length > 0 && (
            <table>
              <thead>
                <tr>
                  <th>Date_Time</th>
                  <th>Trans_no</th>
                  <th>Credit</th>
                  <th>Debit</th>
                  <th>Detail</th>
                </tr>
              </thead>
              <tbody>
                {filteredResults.map((item, index) => (
                  <tr key={index}>
                    {renderCell(item, "date_time")}
                    {renderCell(item, "trans_no")}
                    {renderCell(item, "credit")}
                    {renderCell(item, "debit")}
                    {renderCell(item, "detail")}
                  </tr>
                ))}
              </tbody>
            </table>
          )}

          {/* Phn trang */}
          {filteredResults.length > 0 && (
            <Pagination
              currentPage={currentPage} // Truyn currentPage vo Pagination
              onPageChange={handlePageChange} // Gi hm khi chuyn trang
              aria-label="Pagination"
              lastPage={lastPage}
              className="custom-pagination"
            >

              <Pagination.Controls>
                {/* Nt chuyn v trang u */}
                <button
                  aria-label="Go to First Page"
                  className="page-nav-button"
                  onClick={() => handlePageChange(1)} // m bo truyn `1`  ng nht
                  disabled={currentPage === 1} // V hiu ha nu ang  trang u tin
                >
                  &laquo; First
                </button>

                {/* Nt gim trang */}
                <button
                  aria-label="Previous Page"
                  className="page-nav-button"
                  onClick={() => {
                    if (currentPage > 1) handlePageChange(currentPage - 1);
                  }}
                  disabled={currentPage === 1} // V hiu ha nu ang  trang u tin
                >
                  &lt;
                </button>
                <Pagination.PageList fontSize="500px">
                  {({ state }) => {
                    const maxVisiblePages = 5; // S trang hin th mi ln
                    const startPage = Math.max(
                      1,
                      currentPage - Math.floor(maxVisiblePages / 2)
                    );
                    const endPage = Math.min(
                      totalPages,
                      startPage + maxVisiblePages - 1
                    );

                    const range = Array.from(
                      { length: endPage - startPage + 1 },
                      (_, i) => startPage + i
                    );

                    return range.map((pageNumber) => (

                      <Pagination.PageListItem key={pageNumber} className="page-item">
                        <Pagination.PageButton
                          aria-label={`Page ${pageNumber}`}
                          pageNumber={pageNumber}
                          className={`page-button ${pageNumber === currentPage ? "active" : ""}`}
                          onClick={() => handlePageChange(pageNumber)}
                        >
                          {pageNumber}
                        </Pagination.PageButton>
                      </Pagination.PageListItem>
                    ));
                  }}
                </Pagination.PageList>
                {/* Nt tng trang */}
                <button
                  aria-label="Next Page"
                  className="page-nav-button"
                  onClick={() => {
                    if (currentPage < totalPages) handlePageChange(currentPage + 1);
                  }}
                  disabled={currentPage === totalPages} // V hiu ha nu ang  trang cui cng
                >
                  &gt;
                </button>
                {/* Nt chuyn v trang cui */}
                <button
                  aria-label="Go to Last Page"
                  className="page-nav-button"
                  onClick={() => handlePageChange(totalPages)}
                  disabled={currentPage === totalPages} // V hiu ha nu ang  trang cui cng
                >
                  Last &raquo;
                </button>
              </Pagination.Controls>
              <Pagination.AdditionalDetails shouldHideDetails className="pagination-details">
                {({ state }) =>
                  `${getVisibleResultsMin(state.currentPage, resultCount)}-${getVisibleResultsMax(
                    state.currentPage,
                    resultCount,
                    totalPages
                  )} of ${totalPages} results`
                }
              </Pagination.AdditionalDetails>
            </Pagination>
          )}
        </div>
      )}

      {/* Nu khng tm thy kt qu */}
      {noResults && (
        <div>
          <button onClick={handleReload}>Reload trang</button>
        </div>
      )}
    </div>
  );
}

export default App;
